.link {
    color: #000;
    font-size: 18px;
}

.header {
    background-color: var(--bg-primary);
    box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, .3);
    position: fixed;
    width: 100%;
    z-index: 3;
    top: 0;
    left: 0;
    padding: 0;

}

.header ul {
    margin: 0;
    padding: 0 10px;
    list-style: none;
    overflow: hidden;
    background-color: var(--bg-primary);
}

.header ul .link {
    display: block;
    padding: 20px;
    text-decoration: none;
}

.header ul a:hover {
    background-color: #ddd;
}

.header .logo {
    float: left;
    display: block;
    font-size: 2em;
    padding: 10px 20px;
    text-decoration: none;
    font-weight: bold;
    padding-left: 40px;
}

.header .menu {
    clear: both;
    max-height: 0;
    transition: max-height .2s ease-out;

}


.header .menu-icon {
    padding: 28px 20px;
    position: relative;
    float: right;
    cursor: pointer;

}

.header .menu-icon .nav-icon {
    background-color: #333;
    display: block;
    height: 3px;
    width: 18px;
    position: relative;
    transition: background .2s ease-out;
}


.header .menu-icon .nav-icon:before {
    background-color: #333;
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    transition: all .2s ease-out;
    top: 5px;

}


.header .menu-icon .nav-icon:after {
    background-color: #333;
    content: '';
    display: block;
    height: 100%;
    width: 100%;
    position: absolute;
    transition: all .2s ease-out;
    top: -5px;
}

header .menu-btn {
    display: none;
}

.header .menu-btn:checked~.menu {
    max-height: 100%;
}


.header .menu-btn:checked~.menu-icon .nav-icon {
    background: transparent;

}


.header .menu-btn:checked~.menu-icon .nav-icon:before {
    transform: rotate(-45deg);
    top: 0;


}


.header .menu-btn:checked~.menu-icon .nav-icon:after {
    transform: rotate(45deg);
    top: 0;
}

@media (min-width: 1100px) {
    .header {
        padding: 0 120px;
        display: flex;
        justify-content: space-between;
        align-items: center;

    }

    .header li {
        float: left;
    }

    .header li .link {
        padding: 20px 30px;
    }

    .header .menu {
        clear: none;
        float: right;
        max-height: none;


    }

    .header .menu-icon {
        display: none;
    }


}