/* Footer Section */
.footer-container {
    padding: 60px 0px;
    background: #1c375b;
    color: #f0f0f0;
}



#footer {
    max-width: 1024px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-between;
    padding: 0px 20px;
    flex-wrap: wrap;
}

#footer .logo {
    width: 40px;
}

#footer .logo-container {
    display: flex;
    align-items: center;
    margin-right: 5px;
}

#footer .logo-container p {
    font-size: 26px;
}



#footer .links ul li {
    color: #fff;
    margin: 10px 0px;
    list-style: none;
    font-size: 16px;

}


#footer .links ul li .link {
    color: rgb(184, 184, 184);
    text-decoration: none;
    font-size: 16px;
}

#footer .social .fab {
    color: rgb(243, 117, 0);
    font-size: 28px;
    margin: 0px 5px;
    padding: 10px 0px;
    transition: all ease-in-out .4s;
    cursor: pointer;
}

#footer .social .fab:hover {
    color: white;
}

footer {
    background-color: #000000;
    color: white;
    padding: 26px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
}

#footer .title {
    font-size: 18px;
    color: var(--text-light);

}



.policy-container .links {
    border-top: 2px solid gray;
    border-top-width: 50%;
    margin-top: 5px;
    display: flex;
    justify-content: center;
    padding: 10px;
}

.policy-container .links li {
    margin: 0 10px;
    list-style: none;
    text-align: left;

}

.policy-container .links li a {
    text-align: left;

}

.policy-container .links li a {
    color: rgb(212, 212, 212);
    text-align: left;

}

.icon {
    color: white;
    margin-right: 15px;
}



@media (max-width: 800px) {
    #footer {
        flex-direction: column;
    }

}