.course-container {
    padding: 0px;
    max-width: 1024px;
    margin: 0px auto;
    text-align: center;

}



.course-wrapper {
    padding: 20px;
    position: relative;
    padding: 60px 0;
}


video {
    width: 80%;
}


.course-overlay {
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    width: 100%;
    padding: 100px 60px;
    text-align: left;
    position: relative;

}

.course-overlay>* {
    margin-bottom: 10px;
}

.list-container {
    list-style: none;
    text-align: left;
    list-style-image: url(../../../assets/images/check.png);
    padding: 20px;

}

.list-container h3 {
    margin: 5px 0;
}

.list-container li {
    margin-bottom: 5px;
    padding: 4px;
}

.course-description {
    text-align: left;
    width: 100%;
}

.course-description h2 {
    margin: 5px 0;
}

.course-description p {
    line-height: 1.6;
    font-size: 18px;
}

.course-container .buy-btn,
.c-btn {
    padding: 6px 15px;
    border-radius: 6px;
    outline: none;
    border: none;
    cursor: pointer;
    margin: 10px auto;
    background-color: green;
    color: white;
    box-shadow: 0px 2px 2px 1px rgba(0, 0, 0, 0.3);

}

.buy-btn:hover {
    box-shadow: 0px 2px 2px 1px rgba(0, 0, 0, 0.5);
}

.go-back-btn {
    background-color: darkorange;
    position: fixed;
    top: 0px;
    left: 10px;
    z-index: 200;
}

.rating {
    display: flex;
    margin: 5px 0;

}

.star {
    cursor: pointer;
}

.rating span {
    color: orange;
    font-size: 1.2rem;
    letter-spacing: 4px;
    margin-left: 10px;
}