@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');


:root {
    --bg-primary: #fbfbfd;
    --text-primary: #599ffe;
    --text-secondary: #ac78f9;
    --text-dark: #1f2428;
    --text-medium: #404556;
    --text-ligth: rgba(0, 0, 0, 0.603);
    --text-warning: orange;
    --container-width: 1200px;
    --text-medium-size: 18px;
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    background-color: var(--bg-primary);
}

h1 {
    font-size: 36px;
}

h2 {
    font-size: 30px;
    text-align: center;
}

.heading {
    text-align: center;
    padding-top: 10px;
    padding: 20px 0;
    border-bottom: 2px solid salmon;
    width: fit-content;
    margin: auto;
    margin-bottom: 30px;
}

.p-subtitle {
    padding: 0 20px;
    font-size: 26px;
    margin: 20px auto;
    text-align: center;
}

/* Form */


.form {
    padding: 20px;
    max-width: 600px;
    margin: 20px auto;
    background-color: white;
    box-shadow: 0 2px 2px 2px rgba(0, 0, 0, 0.3);
    border-radius: 10px;


}

.form-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.form input,
textarea {
    padding: 5px 10px;
    font-size: 20px;
    border-radius: 6px;
    outline: none;
    border: 1px solid rgba(0, 0, 0, 0.3);
    background-color: none;
}

.form input::placeholder,
textarea::placeholder {
    opacity: .4;
}

.btn-group {
    display: flex;
    flex-direction: column;
    margin: 10px 0;
}

.error {
    color: rgb(255, 0, 0);
    width: 100%;
    padding: 5px 10px;
    margin-bottom: 50px;
}

.passport-container {
    width: 90px;
    height: 90px;
    margin: auto;
    margin: 10px auto;
}

.passport-label {
    width: 100%;
    height: 100%;
    display: block;
    background: url(./assets/images/user.png) no-repeat center center;
    background-size: cover;
    border-radius: 50%;
    box-shadow: 0px 2px 2px 2px rgba(0, 0, 0, 0.3);
}

#passport {
    display: none;
    width: 0;
    height: 0;
    overflow: hidden;
}