.request-form-container {
    padding: 100px 0;
}


.form-wrapper {
    max-width: 1200px;
    margin: auto;
}

.request-form {
    width: 100%;
    box-shadow: none;
    background-color: inherit;
}

.input-group {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;

}

.form-group label {
    margin-bottom: 10px;
    opacity: .6;
    ;
}

.request-form .input-group .form-group {
    flex: 1;
    margin: 0 10px;

}

.request-form textarea {
    width: 100%;
    height: 100px;
    resize: none;


}

.request-form select {
    padding: 5px 10px;
    outline: none;
    background-color: #fff;
    border-radius: 6px;
    border: 1px solid rgba(0, 0, 0, 0.3);



}



@media (max-width:1100px) {
    .input-group {
        flex-direction: column;
    }

}