.about {}

.container {
    text-align: center;
    margin: auto;



}

.text-bg {
    background: url(../../assets/images/code.jpg) no-repeat center center;
    background-size: cover;
}

.about-overlay {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.4));
    padding: 100px 0 40px 0;



}

.about .text-wrapper {
    max-width: 1024px;
    margin: auto;
}

.about .desc {
    max-width: 600px;
    margin: auto;
    font-size: 20px;
    line-height: 1.6;
    color: white;
    opacity: .8;

}

.about .title {
    font-size: 28px;
    color: white;

}

.about .heading {
    color: white;
}