.home {}


.content-wrapper {
    padding-top: 100px;
    max-width: var(--container-width);
    margin: auto;
    padding: 40px;
}

.intro {
    background: url(../../assets/images/code-bg.jpg) no-repeat center center;
    background-size: cover;
    width: 100%;
}

.overlay {
    text-align: center;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.3));
    height: 100%;
    padding: 20px;
    padding: 100px 20px;

}


.title {
    font-size: 36px;
}

.subtitle {
    font-size: 28px;
}

.image-container {
    margin: auto;
    overflow: hidden;
    margin-top: 40px;
}

.image-container img {
    width: 100%;
    border-radius: 30px;
    object-fit: cover;

}



.description {
    max-width: 600px;
    margin: auto;
    font-size: 26px;
    line-height: 1.6;
    color: white;

}

.home-title {
    color: white;
}

/* Technology */

.technology-container {
    padding: 30px;
    padding-bottom: 70px;
    height: 500px;
}

.technology-btn-wrapper {
    display: flex;
    flex-wrap: wrap;
}

.technology-btn {
    flex: 1;
    padding: 20px;
    cursor: pointer;
    border-bottom: 1px solid rgba(0, 0, 0, 0.493);
}

.technology-btn:hover,
.active {
    border-top: 1px solid rgba(0, 0, 0, 0.493);
    border-bottom: none;
    border-left: 1px solid rgba(0, 0, 0, 0.493);
    border-right: 1px solid rgba(0, 0, 0, 0.493);
}

.technology-wrapper {
    display: flex;
    padding: 20px;
    height: 100px;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}

.technology {
    flex: 1;
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5px;
}

.technology h4 {
    opacity: .7;
    font-size: var(--text-medium-size);
}

.technology img {
    width: 50px;
    height: 50px;
}

.hide {
    display: none;
}