.contact {
    padding-top: 100px;
    height: 100vh;
}

.contact-title {
    margin-bottom: 10px;
    font-size: 18px;
}

.content-wrapper {
    max-width: 1024px;
    margin: auto;
}

.contact .form-group {
    position: relative;
}

.required {
    position: absolute;
    color: red;
    font-size: 18px;
    padding: 5px;
    width: 18px;
    height: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    right: -5px;
    top: -5px;
    font-weight: bold;
}

.message-input {
    resize: none;
    height: 100px;
}

.address {
    display: flex;
    justify-content: space-around;
    padding: 20px;
}

.address li {
    list-style: none;
    margin-bottom: 8px;
    display: flex;
    align-items: center;

}

.address li>* {
    margin-right: 10px;

}

.address li a {
    text-decoration: none;
    font-style: italic;
}

.line {
    width: 2px;
    height: 70px;
    background-color: blue;
    justify-content: center;
    border-radius: 50px;
    /* margin: 10px auto; */
}

@media (max-width: 800px) {
    .address {
        flex-direction: column;
        justify-content: center;
    }

    .line {
        width: 70px;
        height: 2px;
        margin: 10px auto;
    }
}