.services {
    padding: 100px 0;
}



.card-wrapper {
    margin: auto;
    max-width: 1100px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 45px;
    padding: 40px 20px;

}

.card-wrapper .card {
    position: relative;
    background-color: rgba(250, 250, 250, 1);
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.6);
    transition: all ease-in-out 0.4s;
    cursor: default;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    overflow: hidden;
    padding-bottom: 10px;

}

.card-wrapper .card:hover {
    transform: scale(1.06);
}

.card-body {
    padding: 10px;
}

.card-title {
    font-size: 1.1rem;
    color: darkcyan;
    margin: 10px auto;
    text-transform: capitalize;
}



.divider {
    height: 2px;
    width: 80%;
    margin: 0 auto;
    background-color: rgba(0, 0, 0, 0.2);
    border-radius: 50%;
}

.card-image {
    width: 100%;
    background-color: rgb(255, 255, 255);
    transition: all linear 0.2s;
    text-align: center;
}



.card-image img {
    width: 100%;
    transition: linear 0.4s all;
    height: 150px;
    object-fit: cover;
}

.card p,
.card h2 {
    text-align: center;

}



.card p {
    padding: 10px;
    font-size: 1.1rem;
    line-height: 1.4;
    margin: 0;
    text-align: center;
    width: 100%;
}

