.courses {}

.courses-bg {
    background: url(../../assets//images/courses-bg.jpg) no-repeat center;
    background-size: cover;
    background-attachment: fixed;
}

.courses-overlay {
    padding-top: 100px;

    background-image: linear-gradient(to right, rgba(0, 0, 0, .8), rgba(0, 0, 0, .7), rgba(0, 0, 0, .8));


}

h1 {
    font-size: 1.2rem;
    text-transform: uppercase;

}

.courses-header {
    max-width: 600px;
    margin: auto;
    text-align: center;
}

.courses-header p {
    line-height: 1.6;
}

.courses-header h2 {
    text-transform: uppercase;
    font-size: 1.6rem;
    font-weight: 800;
}

.courses-header h2 span {
    color: blue;
}

.latest-course {
    text-align: center;
    margin: 10px 0;
    color: blue;
    font-weight: 800;
}

.latest-course span {
    color: green;
}

.card-wrapper {
    margin: 0 auto;
    max-width: 1300px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    gap: 20px;
}

.card-wrapper .card {
    position: relative;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2), 0px 0px 20px rgba(0, 0, 0, 0.2);
    transition: all ease-in-out 0.4s;
    cursor: default;
    display: flex;
    flex-direction: column;
    text-align: center;
    overflow: hidden;
    max-width: 350px;
    margin: auto;
    background-color: rgba(240, 248, 255, 0.8);
}

.card-wrapper .card:hover {
    transform: scale(1.06);
}

.card-header,
.card-footer {
    padding: 10px;
}



.card-image {
    width: 100%;
    background-color: rgb(255, 255, 255);
    transition: all linear 0.2s;
    text-align: center;
    margin-bottom: 10px;
}

.card-image img {
    width: 100%;
    height: 150px;
    transition: linear 0.4s all;
}

.card .title {
    font-size: 1.2rem;
    margin: 10px 0;
}

.card-footer {
    display: flex;
    flex-direction: column;
    margin: 10px 0;
}

.instructor {
    font-size: .8rem;
    color: brown;
    margin-bottom: 8px;
}


.subtitle {
    color: rgb(0, 139, 139);
    font-size: .8rem;
}

.show-more {
    display: block;
    margin: auto;
    width: 80%;
    padding: 8px;
    border: 1px solid rgba(0, 139, 139, 0);
    outline: none;
    cursor: pointer;
    font-size: 18px;
    border-radius: 8px;
    background-color: darkcyan;
    color: white;
    margin-bottom: 20px;
    transition: .2s linear;

}

.show-more:hover {
    border: 1px solid rgba(0, 139, 139, 1);
    background-color: white;
    color: darkcyan;

}

.rating-container span {
    color: goldenrod;
}

.btn-wrapper {
    padding: 20px 0;
    display: flex;
    justify-content: center;

}